import Vue from 'vue';
import Router from 'vue-router';
import Home from '../pages/Home.vue'
import About from '../pages/About.vue'
import Projects from '../pages/Projects.vue'
import GetStarted from '../pages/GetStarted.vue'
import Skills from '../pages/Skills.vue'
import Contact from '../pages/Contact.vue'
import Translates from '../pages/Translates.vue'
import CreatinganOrganizedReactApp from '../pages/TranslatePosts/CreatinganOrganizedReactApp.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
        path: '/about',
        name: 'About',
        component: About
      },
      {
        path: '/get-started',
        name: 'GetStarted',
        component: GetStarted
      },
      {
        path: '/projects',
        name: 'Projects',
        component: Projects
      },
      {
        path: '/skills',
        name: 'Skills',
        component: Skills
      },
      {
        path: '/contact',
        name: 'Contact',
        component: Contact
      },
      {
        path: '/translates',
        name: 'Translates',
        component: Translates
      },
      {
        path: '/translates/creating-an-organized-react-app',
        name: 'CreatinganOrganizedReactApp',
        component: CreatinganOrganizedReactApp
      }
  ]
})