<template>
    <div class="container">
        <Title text="Minhas habilidades" />
         <p>
            Estes são valores estimados sobre os conhecimentos técnicos que possuo. Adquirindo-os através de cursos ou outras experiências em projetos que trabalhei..<br/>
        </p>
        <div class="chart">
            <pie-chart :data="chartData" :options="chartOptions"></pie-chart>
        </div>

        <div class="tecnologies">
            <div
                v-for="tecnology in tecnologies"     
                v-bind:key="tecnology.id" 
                class="tecnology"
            >
                <h3 :class="tecnology.class">{{tecnology.name}}</h3>
                 <p>{{tecnology.text}}</p>
            </div>
        </div>

    </div>
</template>

<script>
import {Title} from '../components/Sections'
import PieChart from "../helpers/PieChart.js";
import {colors} from '../constants/colors'

export default {
    name:'Skills',
    components: {
        Title, 
        PieChart 
    },
    data() {
        return {
            tecnologies: [
                {
                    id:1,
                    name: 'React',
                    text: `Utilizei ReactJS e ReactNative em projetos pessoais, além de já ter trabalhado profissionalmente com estas tecnologias, portando possuo certa segurança neste assunto. Já realizei cursos como: trilha junior, sênior e pleno sobre ReactJS na Treinaweb, curso de React Native na udemy.`,
                    class: "react"
                },
                {
                    id:2,
                    name: 'HTML5',
                    text: `Desde cedo tive meu primeiro contato com HTML. Aos meus 14 anos, quando ainda existiam anúncios de cursos sobre WEB MASTER e WEB DESIGNER, ou quando ainda se utilizava DREAMWEAVER, eu já estava criando meu primeiro <h1>Olá, mundo</h1>. Desde aquela época acompanhei a evolução desta linguagem de marcação, e venho sempre tentando aprender mais sobre as novidades que o HTML5 trouxe para o mundo web.`,
                    class: "html"
                },
                {
                    id:3,
                    name: 'VueJs',
                    text: `Por mais que não seja a tecnologia que mais conheço, ainda sim, sempre tento aprender mais sobre o VUE, pois sei da sua potencialidade. Ainda estou em andamento com um curso sobre Vue na udemy, além disso, quis me submeter a um desafio com este framework, por isso, este portfolio foi totalmente desenvolvido com Vue. Isso me forçou a aprender coisas como: Animações, gráficos, rotas, estados, organização de pastas, criação de componentes, e pré-processadores css dentro do vue.`,
                    class: "vue"
                },
                {
                    id:4,
                    name: 'CSS',
                    text: `Quando a gente aprende HTML é impossível não se aventurar no CSS, você pode não ser o melhor designer escolhendo cores ou tipografias, mas quando você consegue fazer uma div quadrada e colocar um estilo nela, você realmente começa a AMAR o css.
                    Como eu já disse, conheço HTML a um certo tempinho, e desde então venho aprendendo junto CSS. Hoje, muita coisa se facilitou como CSS3, a chegada do Flexbox, por exemplo, trouxe muito mais agilidade nos processos. Além disso, as animações deram outra cara para as páginas. Possuo bastante conhecimento em CSS, e o que não sei, sempre busco aprender, pois se tem uma coisa eu adoro fazer, é poder dar vida as páginas.`,
                    class: "css"
                },
                {
                    id:5,
                    name: 'UX/UI',
                    text: `Para mim é essencial que um bom desenvolvedor front-end saiba lidar com UX, pois isso vai mudar a forma com que ele vê o produto. A experiência do usuário é algo fundamental em um software, pois é este fator que irá influênciar diretamente no sucesso. Eu já fiz cursos de User Interface e User Experience, muitas vezes passo horas no Figma ou AdobeXD, simplesmente por adorar ainda mais planejar uma aplicação, criar um guia de estilo, e desenhar as telas, do que passar o tempo no próprio código.`,
                    class: "ux"
                },
                {
                    id:6,
                    name: 'Javascript',
                    text: `O javascript é a alma de qualquer aplicação moderna, ou até mesmo em páginas comuns. Pois é o javascript que transforma uma página em algo usável pelo usuário, sem ele as páginas apenas teriam um conteúdo com uma estilização bonita, mas com pouca ou nenhuma interação. Como já trabalhei com React, Vue e Ionic, (onde todas estas possuem como linguagem principal o Javascript), posso dizer que tenho certo domínio nessa tecnologia. Além disso, conheço as principais funções do ES6 e as boas práticas na hora de escrever um código em JS.`,
                    class: "javascript"
                }
            ],
            chartOptions: {
                hoverBorderWidth: 20
            },
            chartData: {
                hoverBackgroundColor: "red",
                hoverBorderWidth: 10,
                labels: ["React", "HTML5", "VueJS","CSS","UX/UI","Javascript"],
                datasets: [
                    {
                        label: "Skills",
                        backgroundColor: [colors.react, colors.html, colors.vue,colors.css,colors.ux,colors.javascript],
                        data: [30, 40, 15,35,35,40]
                    }
                ]
            }
        };
    }
}
</script>

<style lang="scss">
    @import url('../constants/colors.css');
     .container {
        max-width: 768px;
        overflow: hidden;
        margin: 0 auto;
    }

    .chart {
        width:300px;
        margin:1em auto;
    }

    .tecnologies {
        .tecnology {
            margin-bottom: 2em;
        }
    }
</style>